@page{
	size: A5 landscape;
}
.content-boleta{
	display: flex;
	justify-content: center;
	align-items: center;
	padding: 1rem 3rem;
}
.boleta{
	font-family:Arial, Helvetica, sans-serif;
	page-break-after: always
  }
  .data{
	font-family: 'Courier New', Courier, monospace;
  }
  .header {
				  display: grid;
				  width: 100%;
				  align-items: center;
				  grid-template-columns: 1fr 3fr 1fr;
				  margin-bottom: 20px;
			  }
			  .header .logo {
				  width: 50px;
				  height: 50px;
			  }
			  .header .logo img{
				  width: 100%;
				  height: 100%;
			  }
			  .header .nombre {
				  text-align: center;
			  }
			  .header .nombre .titulo {
				  margin: 0;
				  font-size: 20px;
				  text-transform: uppercase;
			  }
				.header .numero h1{
				  text-align: center;
					font-size: 15px;
			  }
			  .tabla .recibo {
				  text-align: end;
				  text-transform: uppercase;
				  font-size: 15px;
				  font-weight: bold;
				  margin-bottom: 10px;
			  }
			  .tabla .tabla-general {
				  width: 100%;
				  border: 1px solid;
				  border-collapse: collapse;
				  text-transform: uppercase;
			  }
			  .tabla .tabla-general .table-cabecera {
					background-color: rgb(150, 150, 150);
				}
			  .tabla .tabla-general .tabla-row {
				  border: 1px solid;
		  font-size: 10px;
			  }
			  .tabla .tabla-general .tabla-row .table-head {
		  padding: 5px 10px;
				  border: 1px solid;
				  text-align: center;
				  font-weight: bold;
			  }
			  .tabla .tabla-general .tabla-row .table-cell {
		  padding: 5px 10px;
				  border: 1px solid;
				  text-align: left;
				  height: 30px;
			  }
			  .tabla .tabla-general .tabla-tipo {
				  width: 100%;
				  border-collapse: collapse;
				  border-style: hidden;
				  margin: 0;
				  padding: 0;
			  }
			  .tabla .tabla-general .tabla-tipo .tabla-tipo-row {
				  border: 1px solid;
			  }
			  .tabla .tabla-general .tabla-tipo .tabla-tipo-row .table-tipo-head {
				  border: 1px solid;
				  text-align: center;
				  font-weight: bold;
			  }
			  .celda-firma {
				  height: 60px;
			  }
			  .firma {
				  display: grid;
				  grid-template-columns: 1fr;
				  grid-template-rows: 1fr;
				  width: 100%;
				  height: 100%;
			  }
			  .firma-1 {
				  display: grid;
				  align-items: end;
				  justify-content: center;
				  width: 100%;
				  height: 100%;
			  }
			  .firma-1-1 {
				  border-top: 1px dashed;
				  text-align: center;
				  text-transform: uppercase;
				  font-weight: bold;
			  }